#about{
    background-color:#717171;
    padding: 10px;
    color: black;
    margin: auto;
    border:5px outset #717171;
}
#about h1{
    font-weight: bold;
}
@media screen and (max-width: 768px){
    #about{
        padding: 5px;
    }
    #about h1{
        font-size: 15px;
        font-weight: bold; 
    }
    #about p{
        margin: 3px 0px;
        font-size:9px
    }
    #about .link{
        font-size: 8px;
        padding: 5px;
    }
}