#movie {
    padding: 10px;
    margin: auto;
}
#movie .card {
    border-color: #fff;
    padding: 15px;
    margin-bottom: 10px;
    background-color: black;
    height: 500px;
    /* width: 319px; */
}
#imgOfCard {
    height: 80%;
    width: 100%;
}
#movie p {
    height: 50px;
    margin: 7px;
    color: #ffff;
    line-height: 20px;
}
#imgOfCard img {
    width: 100%;
    height: 100%;
}
@media screen and (max-width: 768px) {
#movie .card{
    margin-bottom: 5px;
    height: 250px;
    padding: 10px;
    text-align: center;
}
#imgOfCard {
    height: 70%;
    width: 100%;
}
#movie p{
    height: 50px;
    margin: 7px;
    font-size: 8px;
    line-height: 15px;
}
#movie .btn{
font-size: 10px;
width: 100%;
height: 30px;
}
}
