#input{
    background-color:#AAAA;
    margin: 0px 0px 30px 0px;
    padding: 40px;
    text-align: center;
}
#input h2{
    display: inline-block;
}

@media screen and (max-width: 768px) {
    #input {
        padding: 10px;
    }
    #input {
        font-size: 10px;
    }
    #input h2 {
        font-size: 20px;
        }
    #input input{
        padding-left: 5px;
        font-size: 13px;
        width: 100%;
        height: 25px;
    } 
    #input button{
        font-size: 10px;
        height: 30px;
        width: 90px;
    }
}

