#moviesInfo{
   margin: 30px 0px;
}
#moviesInfo .imgOfMovie{
    height: 500px;
    padding: 20px;
    border:4px solid #F1F1F1;
}
#moviesInfo img{
    width: 100%;
    height: 100%;
}
#moviesInfo .disc h2{
 font-size: 35px;
 color: white;
}
.disc p{
    font-size: 18px;
    color: white;
    margin: 0px;
    padding: 10px 0px 4px 0px;
    border-bottom: 2px solid white ;
    font-weight: bold;
}
.disc p span{
    font-weight: normal;
}

@media screen and (max-width: 768px){
    #moviesInfo .imgOfMovie{
        height: 300px;
        padding: 10px;
        border:4px outset #F1F1F1;
    }

    #moviesInfo{
        margin: 10px 5px;
     }

    #moviesInfo .disc h2{
        font-size: 15px;
       }
       .disc p{
           font-size: 9px;
           padding: 5px 0px 2px 0px;
       }
}